#logo {
    position: relative;
    display: grid;
    justify-content: space-evenly;
    width: 100%;
    background-color: #202020;
    color: white;
}

form {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 5px;
    padding: 10%;
}

#content input[type="text"],
#content input[type="number"] {
    width: 80%;
    height: 30px;
    border: 2px solid #ccc;
    font-size: 20px;
    border-radius: 5px;
    padding-left: 10px;
}